.info-content {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 10em;
    left: 10em;
    color: #ffffff;
}

.dog-container { 
    display: flex;
    flex-direction: column;
    margin-left: 15em;
    margin-bottom: 5em;
  }

.dog-image {
    width: 1em;
}

.dog-image img {
    height: 20em;
    width: 20em;
    object-fit: cover;
    border-radius: 1em;
}

.dog-row {
    display: flex;
    flex-direction: row;
    margin-top: 2em;
}

.dog-row1 {
    margin-right: 1em;
}

.dog-column {
    text-align: start;
}

.dog-breed {
    text-transform: capitalize;
}

.owner-info {
    text-align: start;
    margin-top: 1.2em;
}

.back-btn {
    position: absolute;
    right: 18em;
}

.back-btn button {
    background-color: transparent;
    border: transparent;
    color: #ffffff;
    font-family: 'Courier New', Courier, monospace;
    cursor: pointer;
    padding-left: 5em;
    font-size: 0.8em;
}

.arrow {
    border: solid #ffffff;
    border-width: 0 4px 4px 0;
    display: inline-block;
    padding: 3px;
    margin-right: 0.4em;
  }

  .left {
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
  }

